/* Homepage */
/* Section logo list */
.section-logo-list {
    .title {
        font-weight: var(--font-body-weight);  
        padding-bottom: 30px;
        @media (min-width: 992px) {
            padding-bottom: 0;
        }
        strong { 
            font-weight: var(--font-bold);
        }
    }
    .slider-wrapper {
        margin: 0 40px;
    }
    .slider-list {
        align-items: center;
    }
    .slider-controls button {
        background-color: transparent;
        box-shadow: none;
        svg {
            color: #ffffff; 
            path {
                fill: #ffffff; 
            }
        }
        &[disabled] {
            opacity: 1;
            svg path {
                fill: #ffffff;  
            }
        }
        &.slider-prev-button {
            @media (min-width: 768px) {
                left: -20px;
            }
        }
        &.slider-next-button {
            @media (min-width: 768px) {
                right: -20px;
            }
        }
    }
}
/* End section logo list */

/* Section blog grid */
.article-card {
    height: 100%;
    display: flex;
    flex-direction: column;
    min-width: 0;
    .article-card_header {
        position: relative;
        margin-bottom: 17px;
        overflow: hidden;
        img {
            object-fit: cover;
            @include transition-img;
        }
        &:hover {
            @media (hover: hover) {
                img {
                    transform: scale(1.2);    
                } 
            }
        }
    }
    .article-card_published-at {
        font-size: var(--body-font-size);
        color: var(--text-miscellaneous);
        margin-bottom: 2px;
    }
    .article-card_title {  
        margin-bottom: 7px; 
        font-weight: var(--font-body-weight);
        overflow: hidden;
	    white-space: initial;
	    display: -webkit-box;
	    -webkit-line-clamp: 1; 
	    -webkit-box-orient: vertical;
	    word-wrap: break-word;
        text-overflow: -o-ellipsis-lastline; 
    }
    .article-card_excerpt {  
        overflow: hidden;
        margin-bottom: 39px;
	    white-space: initial;
	    display: -webkit-box;
	    -webkit-line-clamp: 2;
	    -webkit-box-orient: vertical; 
	    word-wrap: break-word;
        text-overflow: -o-ellipsis-lastline;
        color: var(--body-color-2); 
        img {
            display: none;
        }
        p:last-child {
            margin: 0;
        }
    }
    .article-card_footer {
        margin-top: auto;  
    }
    .btn {
        display:inline-block;
        padding: 9px 17px;  
        min-width: 114px;
        font-size: calc(var(--btn2-fs) - 2px);
        @media (min-width: 750px) {
            padding-left: 40px;
            padding-right: 40px;
        }
    } 
}
/* End section blog grid */
/* Section Banner multiple */ 
.section-banner-multiple {
    .banner-item {
        overflow: hidden;
        @include transition;
        > a {
            color: inherit;
        }
        > .image__style img, a > .image__style img {
            @include transition-img;
        } 
        &:hover {
            @media (hover: hover) {
                > .image__style img, a > .image__style img {
                    transform: scale(1.2);    
                }  
            } 
        }
    }
}
/* End section Banner multiple */
/* Section Iconbox */
.section-iconbox {
    .iconbox-item {
        display: flex;
        align-items: center;
        .icon {
            flex: 0 0 40px; 
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: rgba(var(--main-color-rgba), 0.1);
            padding: 10px;
        }
        .content { 
            @include padding-start(12px); 
            .heading {
                font-size: var(--body-font-size);
                margin-bottom: 2px;
            }
            .subtext {
                margin: 0;
                color: var(--text-miscellaneous);
            }
        }
    }
}
/* End section Iconbox */
/* Section Iconbox 2*/
.section-iconbox-2 {
    .icon {
        max-width: 80px;
        margin: 0 auto 15px;   
        @media (min-width: 750px) {
            margin-bottom: 22px;
        }    
    } 
    .section-header {
        max-width: none;
        margin: 0;
    }
    .content {
        .heading {
            font-size: var(--body-font-size);
            margin-bottom: 2px;
        }
        .subtext {
            margin: 0;
            color: var(--body-color-2); 
        } 
    } 
}
/* End section Iconbox 2*/
/* Section Featured categories list */
.section-featured-categories-list {
    .category {
        padding: var(--gutter-xs);
        background-color: var(--second-bg-color);  
        @include transition; 
        >a {
            display: block;
        }
        .title {
            font-size: calc(var(--body-font-size) + 2px); 
            padding-top: var(--gutter-xs); 
            color: var(--body-link);
            font-weight: var(--font-body-weight);
            &:hover {
                @media (hover: hover) {
                    color: var(--body-link-hover); 
                }
            }
        }
        &:hover {
            @media (hover: hover) {
                box-shadow: 0px 5px 24px rgba(35, 35, 35, 0.18); 
            } 
        }
    }
    .section-banner { 
        overflow: hidden;
        .image__style {
            height: 100%;
        }
        img {
            @include transition-img;
        }
        .heading {
            margin-bottom: 10px; 
        }
        .subtext {
            margin-bottom: 20px;
            p {
                margin: 0;
                &:not(:last-child) {
                    margin-bottom: 5px; 
                }
            }
        }
        .btn {
            display: inline-block;
            width: 100%;
            padding: 11px 10px;
            @media (min-width: 576px) {
                width: auto;
                padding-left: 30px;
                padding-right: 30px;
            }
        }
        .text-center .btn {
            margin: 0 auto;
        }
        .text-right .btn {
            @include margin-start(auto); 
        }
        &:hover {
            @media (hover: hover) {
                img {
                    transform: scale(1.2); 
                }
            }
        }
    }
}
/* End section Featured categories list */
/* Section single banner */
.section-single-banner { 
    h3 {
        font-size: 36px;
        color: #ffffff;
    }
    .subtext {
        font-size: 24px;
        color: #ffffff; 
    }
    .btn {
        width: 100%;
        margin-top: 20px;
        padding: 12px 15px; 
        display: inline-block;
        @media (min-width: 576px) {
            width: auto;
            padding-left: 30px;
            padding-right: 30px;
            margin-top: 27px;
            min-width: 325px;
        }
    }
    .sing-banner_content {
        width: calc(100% - 30px);
        &.text-center .btn {
            margin-left: auto;
            margin-right: auto;
        }
        &.text-right .btn { 
            @include margin-start(auto);
        }
    }
}
/* End section single banner */
/* Section image gallery */
.section-image-gallery {
    .image__style {
        overflow: hidden; 
        img {
            @include transition-img;
        }
        &:hover {
            @media (hover: hover) {
                img {
                    transform: scale(1.2);
                }
            }
        }
    }
    .image-gallery_title { 
        margin-top: 16px;  
    }
    .image-gallery_subtext {
        margin-top: 6px; 
    }
}
/* End section image gallery */
/* End homepage */